
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    background: {
      type: String,
      required: true,
      validator (val: string) {
        return ['black', 'white'].includes(val)
      }
    },
    highlighted: {
      type: Boolean,
      default: false
    },
    hasPiece: {
      type: Boolean,
      default: false
    }
  }
})
